import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-shipping-bill',
  templateUrl: './shipping-bill.component.html',
  styleUrls: ['./shipping-bill.component.scss']
})
export class ShippingBillComponent implements OnInit {
  dataGhn: boolean = false;
  data_ghtk: boolean = false;
  vietNamPost: boolean = false;
  viettelPost: boolean = false;
  data_ahamove: boolean = false;
  data_delivery: any;
  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhAuth: VhAuth,
    private vhQuerySales: VhQuerySales
  ) { }

  ngOnInit(): void {
    this.getData();
  }
  getData() {
    this.data_delivery = this.vhQuerySales.getlocalDeliverys().filter(item => item.active == true);
    if (this.data_delivery.length) {
      for (let x of this.data_delivery) {
        if (x.type == 1) this.dataGhn = true;
        else if (x.type == 2) this.data_ghtk = true;
        else if (x.type == 3) this.data_ahamove = true;
        else if (x.type == 4) this.vietNamPost = true
        else if (x.type == 5) this.viettelPost = true
      }
    }
  }
  goBack() {
    this.router.navigate(["/sales/dashboard/manage/delivery"]);
  }
  gotoShippingBill(type) {
    if (this.vhAuth.checkMyPermission("shipping_enable_push_bill")) {
      switch (type) {
        case 1: this.gotoGHN(); break;
        case 2: this.gotoGhtk(); break;
        case 3: this.gotoAhamove(); break;
        case 4: this.gotoVnpost(); break;
        case 5: this.gotoViettel(); break;
      }
    }
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoGHN() {
    this.router.navigate(['/sales/dashboard/manage/delivery/shipping-bill/delivery-ghn']);
  }
  gotoGhtk() {
    this.router.navigate(['/sales/dashboard/manage/delivery/shipping-bill/delivery-ghtk']);
  }
  gotoAhamove() {
    this.router.navigate(['/sales/dashboard/manage/delivery/shipping-bill/delivery-ahamove']);
  }
  gotoVnpost() {
    this.router.navigate(['/sales/dashboard/manage/delivery/shipping-bill/delivery-vnpost']);
  }
  gotoViettel() {
    this.router.navigate(['/sales/dashboard/manage/delivery/shipping-bill/delivery-viettel']);
  }
}
