import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ShippingBillComponent } from './shipping-bill.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';

const routes: Routes = [
  {
    path: "",
    component: ShippingBillComponent
  },
  {
    path: "delivery-ghn",
    loadChildren: () => import('./delivery-ghn/delivery-ghn.module').then(m => m.DeliveryGhnModule)
  },
  {
    path: "delivery-ghtk",
    loadChildren: () => import('./delivery-ghtk/delivery-ghtk.module').then(m => m.DeliveryGhtkModule)
  },
  {
    path: "delivery-ahamove",
    loadChildren: () => import('./delivery-ahamove/delivery-ahamove.module').then(m => m.DeliveryAhamoveModule)
  },
  {
    path: "delivery-vnpost",
    loadChildren: () => import('./delivery-vnpost/delivery-vnpost.module').then(m => m.DeliveryVnpostModule)
  },
  {
    path: "delivery-viettel",
    loadChildren: () => import('./delivery-viettel/delivery-viettel.module').then(m => m.DeliveryViettelModule)
  }
]
@NgModule({
  declarations: [ShippingBillComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class ShippingBillModule { }
